@import url("https://fonts.googleapis.com/css2?family=Asap:ital@1&display=swap");

* {
  font-family: "Asap", sans-serif;
}

body {
  display: grid;
  background-color: #fbfbfb;
  color: black;
  height: 100%;
  margin: 0%;
  font-weight: 550;
  user-zoom: none;
  min-width: 100%;
}

header {
  position: fixed;
  left: 0%;
  height: 70px;
  width: 100%;
  background-color: white;
  align-items: center;
  justify-content: left;
  font-size: 25px;
  top: 0%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  box-shadow: 0 3px 10px rgba(24, 24, 24, 0.5);
}

header p {
  width: 100%;
  padding-left: 20px;
}

footer {
  position: fixed;
  left: 0%;
  height: 70px;
  width: 100%;
  background-color: white;
  align-items: center;
  justify-content: left;
  font-size: normal;
  bottom: 0%;
  border-top: 1px solid gray;
  padding-left: 20px;
  display: flex;
  flex-direction: row;
}

footer p {
  font-size: 27px;
}

.addButton {
  background-color: #7719ab;
  width: 59px;
  height: 59px;
  border-radius: 29.5px;
  position: fixed;
  bottom: 9%;
  right: 0%;
  margin: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 40px;
  font-family: "Times new roman";
  cursor: pointer;
  box-shadow: 0 5px 15px rgba(24, 24, 24, 0.5);
  user-select: none;
  transition: transform 250ms;
}

.addButton:active {
  transform: scale(0.8);
}

main {
  min-width: 100%;
}

#holder {
  display: flex;
  flex-direction: column;
  margin-top: 74px;
  margin-bottom: 74px;
  height: 100%;
  width: 100%;
}

#initialAmount,
.thing {
  background-color: #e1f1ff;
  margin: 0%;
  padding: 24px;
  border-radius: 10px;
  border-top: 9px solid #cde9ff;
  margin: 15px;
  margin-bottom: 10px;
  box-shadow: 0 1px 3px rgba(53, 53, 53, 0.5);
  word-wrap: break-word;
  cursor: pointer;
  transition: transform 250ms;
}

.thing {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.thing p {
  margin: 0%;
  padding: 0%;
  word-wrap: break-word;
}

#initialAmount {
  background-color: #e5f9e0;
  border-color: #afeda4;
  padding: 15px;
  padding-left: 15px;
  cursor: default;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

#initialAmount #opener {
  display: flex;
  margin: 0%;
  width: 100%;
  cursor: pointer;
}

#initialAmount #opener p {
  display: grid;
  align-items: center;
  justify-content: center;
}

#initialAmount #saveButton1 {
  font-size: 20px;
  height: fit-content;
  margin-top: 20px;
  padding: 3px;
  display: flex;
  align-items: center;
  border: 3px solid white;
  cursor: pointer;
}

.initialAmountBox {
  display: flex;
  flex-direction: row;
}

.initialAmountBox p {
  font-size: 20px;
  padding-bottom: 0px;
  margin-top: 0%;
  padding-top: 19px;
  border-bottom: 2px solid black;
}

#amountElement {
  border: none;
  border-bottom: 2px solid black;
  outline: none;
  background-color: transparent;
  font-size: 20px;
  height: 20px;
  width: 130px;
  margin: 20px;
  margin-left: 0%;
  font-family: "Asap", sans-serif;
}

.themeChanger {
  width: 40px;
  height: 35px;
  background-color: white;
  border-radius: 25px;
  margin-right: 10px;
  cursor: pointer;
}

.container {
  position: fixed;
  height: 100%;
  width: 100%;
  display: grid;
  justify-content: center;
  align-items: center;
  z-index: 4;
}

.valueInput {
  width: 90%;
  height: 69vw;
  padding-top: 0%;
  padding-left: 3vw;
  border-radius: 20px;
  border-top: 15px solid #c6e7ff;
  background-color: #e1f1ff;
  box-shadow: 0 1px 13px rgba(53, 53, 53, 0.5);
  position: fixed;
  transition: transform 0.2s ease-out;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  font-size: 6vw;

  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

#itemName {
  border: none;
  border-bottom: 2px solid black;
  background-color: #e1f1ff;
  height: 30px;
  width: 40vw;
  margin: 20px;
  font-family: "Asap", sans-serif;
  font-size: inherit;
}

#itemName:focus-visible {
  outline: none;
}

.itemCostDiv {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 10px;
}

#currency {
  color: black;
  margin-left: 10px;
  margin-right: 0px;
  font-size: inherit;
  margin-top: 4px;
  margin-bottom: 2px;
  border-bottom: 2px solid black;
}

#itemCost {
  width: 100px;
  height: fit-content;
  margin: 20px;
  margin-left: 0px;
  border: none;
  border-bottom: 2px solid black;
  background-color: #e1f1ff;
  font-size: inherit;
  font-family: "Asap", sans-serif;
}

#itemCost:focus-visible {
  outline: none;
}

.valueInputButtons {
  color: black;
  margin: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

#saveButton,
#deleteButton {
  border: 2px solid white;
  border-radius: 10px;
  font-size: inherit;
  padding: 5px;
  margin: 7px;
  cursor: pointer;
}
